* {
    margin: 0;
    padding: 0;
    list-style: none;
    -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
    -webkit-appearance: none;
    font-family: "Helvetica Neue", Helvetica, "Hiragino Sans GB", "Microsoft YaHei", Arial, sans-serif;
    font-style: normal;
    outline: none;
}

input,
button,
select,
textarea,
a {
    appearance: none;
    tap-highlight-color: rgba(255, 255, 255, 0);
    -webkit-appearance: none;
    -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
    font-family: "Helvetica Neue", Helvetica, "Hiragino Sans GB", "Microsoft YaHei", Arial, sans-serif;
}

a {
    text-decoration: none;
}

a:hover,
a:focus {
    text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-size: 0.28rem;
    font-weight: normal;
    font-family: "Helvetica Neue", Helvetica, "Hiragino Sans GB", "Microsoft YaHei", Arial, sans-serif;
}

input[type="text"],
input[type="checkBox"],
input[type="password"],
button {
    -webkit-appearance: none;
    appearance: none;
    outline: none;
    border-radius: 0;
    width: 100%;
}

.size(@width;
@height) {
    width: @width;
    height: @height;
}


