.ListOfBluetoothCSS {
    @rem: 144rem;
    .operation{
        text-align: center;
        padding: 0.6em;
        display: flex;
        .button{
            flex: 1;
            background-color: #3492e9;
            color: #FFFFFF;
            .icon.ion-load-c{
                display: inline-block;
                animation: spin 1.8s infinite linear;
                @keyframes spin{
                    0% {
                        transform: rotate(0deg);
                    }
                    100% {
                        transform: rotate(359deg);
                    }
                }
            }
        }
    }
    .list{
        padding-bottom: 0.6rem;
        .card{
            margin: 20px 10px;
            font-size: 1.2em;
            padding: 0.3em;
            margin: 0.6em;
        }
        .card:first-child{
            margin-top: 0px;
        }
        .card:last-child{
            margin-bottom: 0px;
        }
    }
}