.cdListCSS {
  @rem: 144rem;
  background-color: #fff;
  .item-icon-right .icon{
    right: 0;
  }
  .item-icon-right {
    padding-right: 34px;
  }
  .item-icon-left .icon, .item-icon-right .icon {
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    -moz-align-items: center;
    align-items: center;
    top: 0;
    height: 100%;
    font-size: 14px;
    color: #ddd;
  }
  .content {
    h3 {
      line-height: 120/@rem;
      color: #1296db;
      margin-bottom: 0px;
      background: #f4f4f4;
      span {
        padding: 0 10px;
      }
    }
    ul li {
      .leftShow{
        display: inline-block;
        .name{
          font-size: 16px;
        }
        .glassname{
          font-size: 12px;
          color: #999;
        }
        span {
          display: block;
        }
      }
      .rightShow{
        display: inline-block;
        float: right;
        span{
          transform: translateY(10px);
          margin-top: 0px;
          display: block;
        }
        .totalTime{
          margin-top: 20px;
          font-size: 12px;
          color: #3492e9;
        }
      }

    }
  }

}