.homePageCSS {
  @rem: 144rem;
  background-color: #f4f4f4;
  .wrap {
    height: 550/@rem;
  }
  .handImage {
    background: url(../images/homePage/banner2.png) no-repeat center;
    background-size: 100%;
    //height: 1280/@rem;
    height: 3.5rem;
    position: relative;
  }
  .handweather{
    position: absolute;
    bottom: 0px;
    right: 10px;
    color: #ffffff;
  }
  .c_red {
    color: white;
    border-radius: 15px;
    border: 1px solid red;
    font-size: 16px;
    width: 30px;
    height: 30px;
    display: inline-block;
    text-align: center;
    vertical-align: middle;
    /*transform: translate;*/
    transform: scale(0.5) translateX(30px) translateY(-5px);
    line-height: 30px;
    margin-left: 6px;
    background-color: red;
    font-weight: 700;
  }
  .wrap img {
    width: 100%;
    height: 550/@rem;
  }
  .col-33 a {
    display: block;
  }
  .slider .slider-pager {
    bottom: 60/@rem;
    text-align: center;
    padding-right: inherit;
  }
  .slider-pager .slider-pager-page {
    display: none;
    margin: 0px 3px;
    width: 20px;
    color: black;
    text-decoration: none;
    font-size: 12px;
  }
  .rowLa {
    margin-top: 20/@rem;
  }
  .col-33,
  .col-34 {
    background-color: #fff;
    text-align: -webkit-center;
    a {
      color: #000;
    }
  }
  .col-20 {
    background-color: #fff;
    text-align: -webkit-center;
    a {
      color: #000;
    }
  }
  .rowLation {
    display: flex;
    justify-content: space-around;
    height: 359/@rem;
  }
  .rowLation img {
    width: 120/@rem;
    height: 120/@rem;
    transform: translateY(50/@rem);
  }
  .rowLation p {
    text-align: center;
    transform: translateY(85/@rem);
    font-size: 38/@rem;
    align-content: center;
    color: #000000;
    margin: 0;
    font-weight: bold;
  }
  .col-33 {
    height: 359/@rem;
    padding: 0;
  }
  .col-33 a {
    display: inline-block;
    text-align: center;
    align-content: center;
    width: 100%;
    height: 100%;
  }
  .col-20 {
    height: 290/@rem;
    padding: 0;
  }
  .col-20 a {
    display: inline-block;
    text-align: center;
    align-content: center;
    width: 100%;
    height: 100%;
  }
  .row {
    padding: 0px;
  }
  .olList {
    /*padding: 0 45/@rem;*/
    padding: 0 10px;
  }
  .olItem {
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    font-size: 0.33333333rem;
    width: 70%;
    padding: 10px 0px 10px 0;
  }
  .date_msg {
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    font-size: 0.33333333rem;
    padding: 10px 0px 10px 0;
    color: #ccc;
  }
  .new_lo {
    display: flex;
    border-bottom: 1px solid #dddddd;
    justify-content: space-around;
  }
  /*显示天气*/
  .weather {
    //margin-top: 10px;
    /*border-top: 1px solid #dddddd;*/
    background: #fff;
    padding: 15px 5px 15px 5px;
  }
  .weather .weatherShow {
    display: inline-block;
  }
  .weather .weatherShow span {
    color: #000;
    display: inline-flex;
  }
  .listCss {
    margin-top: 5px;
    background-color: white;
  }
  //菜单导航
  .navList {
    margin-top:5px;
  }
  .navList ul {
    font-size: 0;
    background-color: #fff;
  }
  .navList ul li {
    display: inline-block;
    padding: 0rem;
    text-align: center;
    font-size: 0.27777778rem;
    border-bottom: 1px solid rgba(208, 234, 234, 0.2);
    border-right: 1px solid rgba(208, 234, 234, 0.2);
  }
