.facilityInfoCSS {
  @rem: 144rem;
  background-color: #fff;
  .tabs-positive > .tabs, .tabs.tabs-positive {
    border-color: #fff;
    background-color: #fff;
    background-image: linear-gradient(0deg, #0c60ee, #0c60ee 50%, transparent 50%);
    color: #000;
  }
  .tab-item.tab-item-active {
    opacity: 1;
    color: #3492e9;
    border-bottom: 3px solid #3492e9;
  }
  .tab-item.tab-item-active span {
    color: #3492e9;
  }
  .loadList {
    margin-top:120/@rem;
  }
  .facilityList {
    background: #fff;
  }
  .scanQRcode {
    padding: 5px;
    border-bottom: 1px solid #f4f4f4;
    background: #fff;
    text-align: center;
    display: block;
    text-align: center;
    color: #1296db;
    .scanCode {
      background: url(../images/OSI/scanQRcode_icon.png) no-repeat right;
      display: block;
    }
  }
  .scanQRcodeNumber{
    padding: 5px 10px;
    border-bottom: 1px solid #f4f4f4;
    background: #fff;
    display: block;
    color: #1296db;
  }
  .list {
    /*padding: 20/@rem;
    background: #fff;*/
    margin-bottom: 20/@rem;
    .titleDes {
      font-size: 40/@rem;
      line-height: 100/@rem;
      text-overflow: ellipsis;
      white-space: nowrap;
      color: #1296db;
      overflow: hidden;
    }
    .titleDes em {
      float: right;
    }

    .author {
      font-size: 40/@rem;
      /*line-height: 100/@rem;*/
    }
    .author em {
      float: right;
      color: #999;
    }
  }
  .list{
    .card{
      padding: 0rem 0.24rem;
      display: flex;
      margin: 0.18rem;
      span{
        padding: 0.24rem 0rem;
        flex: 1;
      }
      span.flex3{
        flex: 3;
      }
    }
    .card:first-child{
      margin-top: 0rem;
    }
  }




}