.erpCSS{
  @rem: 144rem;
  .card{
    padding: 1em;
    white-space: normal;
    margin: 1.2em 0rem;
  }
  .card:first-child{
    margin-top: 0rem;
  }
  .card:last-child{
    margin-bottom: 0rem;
  }
  .time{
    color: #8e8e8e;
    padding-bottom: 1em;
  }
   .text{
    font-size: 0.8em;
  }
  .details{
    border-top: 1px solid #b8b8b8;
    margin-top: 1em;
    padding-top: 1em;
    display: flex;
    justify-content: space-between;
  }
  .bar.bar-footer{
    padding: 0px;
    border-top: 1px solid #1296db;
  }
  .bar.bar-footer > span{
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .bar.bar-footer > span:not(:first-child){
    border-left: 1px solid #b8b8b8;
  }
}