.erpMyCSS{
    @rem: 144rem;
    .query{
        margin: -10px;
        margin-bottom: 20px;
    }
    .input-label{
        width: 25%;
    }
    input[type="text"], .input-label{
        font-size: 0.8em;
    }
    input[readonly]:not(.cloned-text-input).rightselect{
        border: none;
        background: url(../images/PM/rightselect_icon.png) no-repeat right;
        color: #999;
        text-align: right;
    }
    .inquiryBtn{
        border: none;
        background-color: #3492e9 !important;
        color: #fff;
        font-size: 1.2em;
        padding: 0.5em;
    }
    .dataList{
        display: none;
    }
}